/* modalwallet.css */
.modal-walletBHRT {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /*background-color: rgba(0, 0, 0, 0.5);*/
  background-color: #222;
  z-index: 9999;
}

.modal-walletBHRT.is-open {
  display: block;
}

.modal-walletBHRT-container {
  position: relative;
  width: 80%;
  max-width: 80%;
  margin: 20px auto;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
}

.modal-walletBHR-container {
  position: relative;
  width: 80%;
  max-width: 80%;
  margin: 20px auto;
  background-color: #222;
  background: #222;
  padding: 20px;
  border-radius: 8px;
}

.modal-walletBHRT-close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.content-balance {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.content-details-BHRT {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.botones-wallet-canje-BHRT {
  display: inline-block;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.botones-wallet-canje-BHRT button {
  width: 150px;
  height: 150px;
  display: inline-block;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: none;
  background-color: #222;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.botones-wallet-canje-BHRT button:hover {
  background-color: #dcdcdc;
}

/* Adjust styles as needed for the claim button at the bottom */
.claim-button {
  width: 200px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  background-color: #2ecc71;
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.claim-button:hover {
  background-color: #27ae60;
}


/* modalwallet.css */
.botones-wallet-canje-BHRT button {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.botones-wallet-canje-BHRT button spam {
  align-self: flex-start;
  margin-bottom: auto;
}

.botones-wallet-canje-BHRT button img {
  width: 80%;
