/* ...otros estilos existentes... */

/* Ajustes para dispositivos móviles */
@media (max-width: 768px) {
  .content-balance {
    position: static;
    transform: none;
    text-align: center;
    margin-bottom: 20px;
  }

  .botones-wallet-canje-BHRT {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .botones-wallet-canje-BHRT button {
    width: 45%;
    height: 120px;
  }

  .claim-button {
    width: 90%;
    height: 40px;
  }
}


