.card-info {
  position: relative;
  width: 100%;
  height: 100%;
  background: #2e2e2e;
  display: flex;

}



.card-info .content {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #2e2e2e;
  overflow: hidden;
  z-index: 10;
}


.card-info .content-img {
  display: flex;
  position: relative;
  max-width: 30vw;
  max-height: 30vh;
  border-radius: 30%;
  top: 10px ;
  overflow: hidden;
  z-index: 10;
}



.card-info .content-img h4{
    fontFamily: 'cursive';
    z-index:998; 
    fontSize: '1.2em';
    fontWeight: 'bold'; 
    textTransform: 'uppercase';
    color:'#854D0D';
    textAlign: 'right';
    width: '60%';
    margin: 0;
    color: 'gold';
textShadow: '2px 2px 0 #a98e27, 4px 4px 0 #854D0D';
 
}


.card-info .content p.Status{
z-index:19;
position: relative;
left: 4em;
top:  2em;
text-shadow: 
      1px -1px 0 #767676, 
      -1px 2px 1px #737272, 
      -2px 4px 1px #767474, 
      -3px 6px 1px #787777, 
      -4px 8px 1px #7b7a7a, 
      -5px 10px 1px #7f7d7d;
font-style :italic;
font-weight:1px;
text-transform: initial;
color: gold;
}








