.card-info .sexo{
z-index:15;
position: absolute;
top: 0px;
left: 1px;
display: flex;
}


.card-info .content .icon {
  color: var(--color);
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  background: #2e2e2e;
  box-shadow: 0 0 0 4px #2e2e2e,
    0 0 0 6px var(--color);
  transition: 0.5s ease-in-out;
}

.card-info:hover .content .icon {
  background: var(--color);
  color: #2e2e2e;
  box-shadow: 0 0 0 4px #2e2e2e,
    0 0 0 300px var(--color);
}

.card-info .content .text h3 {
  color: #fff;
  font-size: 1.5rem;
  font-weight: 500;
  transition: 0.5s ease-in-out;
}

.card-info:hover .content .text h3 {
  color: #2e2e2e;
}

.card-info .content .text p {
  color: #999;
  transition: 0.5s ease-in-out;
}



.card-info .content .text a {
  position: relative;
  display: inline-block;
  background: var(--color);
  color: #2e2e2e;
  text-decoration: none;
  font-weight: 500;
  margin-block-start: 10px;
  padding: 8px 15px;
}

.card-info:hover .content .text a {
  background: #2e2e2e;
  color: var(--color);
}






.card-info-c {
  position: relative;
  width: 100%;
  height: 100%;
  background: #ffff;
  display: flex;

}

.card-info-c .content {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 1px 1px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background:#55686f;
  overflow: hidden;
  z-index: 10;
}


.card-info-c .content.GanadasTotal{
z-index:15;
position: absolute;
text-align:left;
border-color: black;
background: blue;
}

.card-info-c .content .GanadasTotal .svg{
width: 2rem;
height: 2rem;
fill:white;
}





