.carreras-container {
  text-align: center;
  padding-top: 2%;
  z-index: 10;
  background-image: url(https://www.blockhorseracing.com/image/fondos/race.png);
  background-size: cover;
  background-position: center;
}

.carreras-container h1 {
  color: #167CBB;
  font-size: 1.5em;
}

.race-list {
  justify-content: space-evenly;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.no-data {
  text-align: center;
  padding: 1rem;
  color: #777;
  font-style: italic;
}

.filtro-race-button {
  background-color: rgba(0, 25, 0, 0.8);
  border: 2px solid #167CBB;
  color: white;
  font-size: 1em;
  padding: 1.5em 2em;
  cursor: pointer;
  transition: 0.3s;
  font-family: Arial, sans-serif;
  margin: 1em;
}

.filtro-race-button:hover {
  background-color: #167CBB;
  color: #000;
}

.pick-six-button {
  background-color: #1E3A8A;
  color: white;
  padding: 15px 30px;
  border: 2px dashed #00FFFF;
  border-radius: 15px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: 0.3s;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.6), 0 0 30px rgba(0, 255, 255, 0.4);
}

.pick-six-button:hover {
  box-shadow: 0 0 25px rgba(0, 255, 255, 1), 0 0 50px rgba(0, 255, 255, 0.8);
  transform: scale(1.05);
}

.pick-six-button:active {
  background-color: #10B981;
  box-shadow: 0 0 15px rgba(16, 185, 129, 0.6), 0 0 30px rgba(16, 185, 129, 0.4);
  transform: scale(0.95);
}


.jornada-info-button {
  background-color: #1E3A8A;
  color: white;
  padding: 0.2em 0.5em;
  border: 2px dashed darkred;
  border-radius: 15px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: 0.3s;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.6), 0 0 30px rgba(0, 255, 255, 0.4);
}

.jornada-info-button:hover {
  box-shadow: 0 0 25px rgba(0, 255, 255, 1), 0 0 50px rgba(0, 255, 255, 0.8);
  transform: scale(1.05);
}

.jornada-info-button:active {
  background-color: #10B981;
  box-shadow: 0 0 15px rgba(16, 185, 129, 0.6), 0 0 30px rgba(16, 185, 129, 0.4);
  transform: scale(0.95);
}


.race-baselight,
.race-highlight {
  z-index: 10;
  font-weight: bold;
}

