.carreras-container li:hover {
  background-color: mediumslateblue;
  color: #423F3F;
}

.carreras-container button.apoya {
  background-color: #14a73e98;
  border: none;
  border-radius: 25px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  color: gold;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  padding: 0.6rem 1.8rem;
  transition: 0.5s;
}

.carreras-container button.apoya:hover {
  background-color: darkgoldenrod;
  color: black;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
  transform: scale(0.8);
}

.carreras-container button.apoya:active {
  animation: shrinkExpand 1s ease;
}

/* Circles */
.circle {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: inline-block;
  animation: pulse 1s infinite;
}

.green {
  background-color: green;
  border: 0.2rem solid darkgreen;
}

.yellow {
  background-color: yellow;
  border: 0.2px solid darkgoldenrod;
}

.red {
  background-color: red;
  border: 0.2rem solid darkred;
}

/* Media Query */
@media screen and (max-width: 768px) {
  .carreras-container h1 {
    font-size: 1.2em;
  }
  
  .carreras-container li {
    flex-direction: column;
    width: 90%;
  }
}

/* Popup */
.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
}
