.wallet {
  display: flex;
  padding-right: 0.2em;
  padding-bottom: 0.2em;
  align-items: center;
  /*padding-right: 1px;*/
  cursor: pointer;
  flex-direction: row-reverse; /* Colocar el div al lado derecho */
}

.wallet div {
  min-width: 2rem;
  min-height: 2rem;
  background-color: gold;
  border-radius: 20%;
 justify-content: center;

}

.wallet svg {
  width: 1.6rem;
  height: 1.8rem;
  /*fill: white;*/
}


.wallet img{
  z-index: 1;
    width: 2.5em;
    height: 2.5em;
}
/* Estilo para la tabla */
table {
  border-collapse: separate;
  border-spacing: 0.5em;
  border-radius: 1em;
  overflow: hidden; /* Para que los bordes ovalados no se salgan */
  background-color: transparent;
}

td {
  background-color: #222;

  border-radius: 1em;
}




.walletBalanceTabla{
display: grid;
}

/* Estilo para el span */
.wallet span {
  display: table-cell;

  background-color: #222;
  color: gold;
  width: 100%;
  /*padding: 2px 4px;*/
  border-radius: 1px;
  font-size: 0.8em;
 
}

/* Estilo para el botón */
button {
  display: inline-block;
  background-color:#222;
  color: gold;
  padding: 0em 0.3em;
  border-radius: 0.5em;
  font-size: 1;
  cursor: pointer;
  box-shadow: 0 1em 4em rgba(218, 165, 32, 0.5); /* Sombra dorada para dar profundidad */

}

button:hover {
  background-color:#167CBB ;
  color: black;
}


