 .card .box .content .arrow {
  display: inline-block;
  opacity: 0;
  transition: opacity 0.3s;
}

.card .box .content:hover .arrow {
  opacity: 1;
}

.card .box .content .arrow {
  animation: fade 2s infinite;
}

@keyframes fade {
  0%, 25% {
    opacity: 1;
  }
  33.33%, 58.33% {
    opacity: 0;
  }
  66.66%, 91.66% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


.icono-caballos {
position: absolute;
background: white;
border-spacing: 3px;
page-break-after: 5px;
margin: 5%;
width: 15%;
height: 15%;
 border: 3px solid #ffffff;
 cursor: pointer;
}


.brightness {filter: brightness(250%);}
.contrast {filter: contrast(180%);}
.grayscale {filter: grayscale(100%);}
.huerotate {filter: hue-rotate(180deg);}
.invert {filter: invert(100%);}
.opacity {filter: opacity(50%);}
.saturate {filter: saturate(7);}
.sepia {filter: sepia(100%);}
.shadow {filter: drop-shadow(8px 8px 10px green);}


 .container-dash .card .box .content h2 {
  position: absolute;
  top: -10px;
  right: 30px;
  font-size: 8rem;
  color: rgba(255, 255, 255, 0.1);
}

 .container-dash .card .box .content h3 {
  font-size: 1.8rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-bottom: 15px;
}

 .container-dash .card .box .content p {
  font-size: 0.9rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}




 .container-dash .card .box .content input{
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: black;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}


 .container-dash .card .box .content input:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}


.modal-close-info{
  background: red;
  z-index: 50;
   align-self: flex-end;
    font-size: 20px;
    cursor: pointer;
    color: #222;
      position: absolute;
    top: 1rem;
    right: 1rem;
}




 