@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}


.container-inventario-Alimentos {
  background: linear-gradient(135deg, rgba(22, 124, 187, 0.3), rgba(213, 75, 75, 0.3), rgba(66, 63, 63, 0.3));
  /*background-image: url(https://www.blockhorseracing.com/image/backgroundballos/track2.jpg);*/
  background-size: cover;  /* Añade esta propiedad para que la imagen se adapte y cubra el fondo */
  background-position: center;  /* Centra la imagen en el fondo */
  margin-left: 10vh;
  margin-right: 10vh;
}

.wrapp {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100%, 100%));
  gap: 2rem;
  justify-content: center;
}


.claim-button-wrapper {
  margin-top: 10px; /* Espacio entre walletData y el botón */
}








.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 10vh;
 
}



 .wrapp .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 100%;
  margin: 0px 0;
}

.container .card {
  position: relative;
  min-width: 260px;
  height: 360px;
  box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.2),
  inset -4px -4px 15px rgba(255, 255, 255, 0.1),
  4px 4px 15px rgba(0, 0, 0, 0.3), -4px -4px 15px rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  margin: 8px;
  transition: 0.5s;
}





 .container .card .box-nft {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: #2a2b2f;
  border-radius: 15px;
  display: block;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.5s;
}


 .container .card .box-nft:hover {
  transform: translateY(-50px);
}

 .container .card .box-nft:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.03);
}

 .container .card .box-nft .content  {
  padding: 35px;
  text-align: center;
}





 .cajas {
  position: relative;
  border-radius: 8px;
  border: 3px solid #12897A;
  width: 100%;
  background-size: cover;
}



.cajas img {
  width: 100%;
 border: 2px solid transparent;
 top: 2px;
 
 }

 .identidad {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  color: white;
  border-radius: 40%;
  
}

.nombre {
  position: absolute;
  bottom: -2%;
  transform: translateY(-50%);
  color: white;
  padding: 1%;

  
  
}


 .cajas svg{
width: 0.2rem;
height: 0.2rem;
fill:white;
}

.icono {
position: absolute;
background: white;
border-spacing: 3px;
page-break-after: 5px;
margin: 5px;
width: 55%;
height: 55%;
 border: 3px solid #ffffff;
}
