 .container .card .box-nft .content h2 {
  position: absolute;
  top: -10px;
  right: 30px;
  font-size: 8rem;
  color: rgba(255, 255, 255, 0.1);
}

 .container .card .box-nft .content h3 {
  font-size: 1.8rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-bottom: 15px;
}

 .container .card .box-nft .content p {
  font-size: 1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}




 .container .card .box-nft .content input{
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  /*background: black;*/
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}


 .container .card .box-nft .content input:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}


.container .card .box-nft .content  .inpu .input-price{
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: black;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 2px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

 .input-container {
  position: relative;
  display: flex;
  justify-content: space-between;

  
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 1px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}




 .container .card .box-nft .content .inpu input:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}

/* Media Query para pantallas de 750px o menos */
@media (max-width: 750px) {
  .cofres-disponibles {
    flex-direction: flex;
    align-items: center;
    min-width: 220px;
  
  }

}